

import { mapStores } from 'pinia';
import Vue from 'vue';

import useSettingsStore from '@/store/SettingsStore';
import Utils from '@/utils';
import SettingsBase from '@/views/Settings/Base.vue';

//  NVRAM / LocalStorage 
const NVRAM_LOCAL_STORAGE_PREFIX = 'KonomiTV-BMLBrowser_nvram_prefix=receiverinfo%2F';

export default Vue.extend({
    name: 'Settings-DataBroadcasting',
    components: {
        SettingsBase,
    },
    data() {
        return {

            // 
            is_form_dense: Utils.isSmartphoneHorizontal(),

            // 
            data_broadcasting_zip_code: '' as string,

            // 
            data_broadcasting_zip_code_validation: (value: string) => {
                if (value === '') {
                    return true;
                }
                if (value.match(/^[0-9]{3}-[0-9]{4}$/) === null) {
                    return '000-0000';
                }
                return true;
            },

            // 
            data_broadcasting_prefecture: '255-0b0' as string,
            data_broadcasting_prefectures: [
                {text: '', value: '255-0b0'},
                {text: '', value: '2-0b000101101011'},
                {text: '', value: '1-0b000101101011'},
                {text: '', value: '3-0b010001100111'},
                {text: '', value: '4-0b010111010100'},
                {text: '', value: '5-0b011101011000'},
                {text: '', value: '6-0b101011000110'},
                {text: '', value: '7-0b111001001100'},
                {text: '', value: '8-0b000110101110'},
                {text: '', value: '9-0b110001101001'},
                {text: '', value: '10-0b111000111000'},
                {text: '', value: '11-0b100110001011'},
                {text: '', value: '12-0b011001001011'},
                {text: '', value: '13-0b000111000111'},
                {text: ' ()', value: '14-0b101010101100'},
                {text: ' ()', value: '49-0b101010101100'},
                {text: '', value: '15-0b010101101100'},
                {text: '', value: '16-0b010011001110'},
                {text: '', value: '17-0b010100111001'},
                {text: '', value: '18-0b011010100110'},
                {text: '', value: '19-0b100100101101'},
                {text: '', value: '20-0b110101001010'},
                {text: '', value: '21-0b100111010010'},
                {text: '', value: '22-0b101001100101'},
                {text: '', value: '23-0b101001011010'},
                {text: '', value: '24-0b100101100110'},
                {text: '', value: '25-0b001011011100'},
                {text: '', value: '26-0b110011100100'},
                {text: '', value: '27-0b010110011010'},
                {text: '', value: '28-0b110010110010'},
                {text: '', value: '29-0b011001110100'},
                {text: '', value: '30-0b101010010011'},
                {text: '', value: '31-0b001110010110'},
                {text: '', value: '32-0b110100100011'},
                {text: '', value: '33-0b001100011011'},
                {text: '', value: '34-0b001010110101'},
                {text: '', value: '35-0b101100110001'},
                {text: '', value: '36-0b101110011000'},
                {text: '', value: '37-0b111001100010'},
                {text: '', value: '38-0b100110110100'},
                {text: '', value: '39-0b000110011101'},
                {text: '', value: '40-0b001011100011'},
                {text: '', value: '41-0b011000101101'},
                {text: '', value: '42-0b100101011001'},
                {text: '', value: '43-0b101000101011'},
                {text: '', value: '44-0b100010100111'},
                {text: '', value: '45-0b110010001101'},
                {text: '', value: '46-0b110100011100'},
                {text: ' ()', value: '47-0b110101000101'},
                {text: ' ()', value: '50-0b110101000101'},
                {text: '', value: '48-0b001101110010'},
            ],
        };
    },
    computed: {
        // SettingsStore  this.settingsStore 
        // ref: https://pinia.vuejs.org/cookbook/options-api.html
        ...mapStores(useSettingsStore),
    },
    created() {
        //  LocalStorage 
        const zip_code_raw = localStorage.getItem(`${NVRAM_LOCAL_STORAGE_PREFIX}zipcode`);
        if (zip_code_raw) {
            try {
                // 34 - 
                this.data_broadcasting_zip_code = window.atob(zip_code_raw);
                this.data_broadcasting_zip_code = this.data_broadcasting_zip_code.slice(0, 3) + '-' + this.data_broadcasting_zip_code.slice(3);
            } catch (error) {
                // 
            }
        }
        //  LocalStorage 
        //  regioncode 
        const prefecture_raw = localStorage.getItem(`${NVRAM_LOCAL_STORAGE_PREFIX}prefecture`);
        if (prefecture_raw) {
            try {
                const prefecture = window.atob(prefecture_raw).charCodeAt(0);
                for (const item of this.data_broadcasting_prefectures) {
                    if (item.value.startsWith(`${prefecture}-`)) {
                        this.data_broadcasting_prefecture = item.value;
                        break;
                    }
                }
            } catch (error) {
                // 
            }
        }
    },
    watch: {
        data_broadcasting_zip_code(new_value: string) {
            // 
            if ((this.$refs.data_broadcasting_zip_code as any).validate() === false) {
                return;
            }
            //  LocalStorage 
            if (new_value !== '') {
                // -  Base64 
                const zip_code_raw = window.btoa(new_value.replace('-', ''));
                localStorage.setItem(`${NVRAM_LOCAL_STORAGE_PREFIX}zipcode`, zip_code_raw);
            } else {
                //  LocalStorage 
                localStorage.removeItem(`${NVRAM_LOCAL_STORAGE_PREFIX}zipcode`);
            }
        },
        data_broadcasting_prefecture(new_value: string) {
            //  LocalStorage 
            if (new_value !== '255-0b0') {
                const value = new_value.split('-0b');
                const prefecture_number = parseInt(value[0]);
                const prefecture_raw = window.btoa(String.fromCharCode(prefecture_number));
                localStorage.setItem(`${NVRAM_LOCAL_STORAGE_PREFIX}prefecture`, prefecture_raw);
                const region_code_number = parseInt(value[1], 2);  // 210
                const region_code_raw = window.btoa(String.fromCharCode(region_code_number >> 8, region_code_number & 0xff));
                localStorage.setItem(`${NVRAM_LOCAL_STORAGE_PREFIX}regioncode`, region_code_raw);
            } else {
                //  LocalStorage 
                localStorage.removeItem(`${NVRAM_LOCAL_STORAGE_PREFIX}prefecture`);
                localStorage.removeItem(`${NVRAM_LOCAL_STORAGE_PREFIX}regioncode`);
            }
        }
    },
    methods: {
        resetNVRAMSettings() {
            // KonomiTV-BMLBrowser_nvram_  LocalStorage 
            for (const key in localStorage) {
                if (key.startsWith('KonomiTV-BMLBrowser_nvram_')) {
                    localStorage.removeItem(key);
                }
            }
            this.data_broadcasting_zip_code = '';
            this.data_broadcasting_prefecture = '255-0b0';
            this.$message.success('');
        }
    }
});

