

import { mapStores } from 'pinia';
import Vue from 'vue';

import Twitter from '@/services/Twitter';
import useSettingsStore from '@/store/SettingsStore';
import useUserStore from '@/store/UserStore';
import Utils from '@/utils';
import SettingsBase from '@/views/Settings/Base.vue';

export default Vue.extend({
    name: 'Settings-Twitter',
    components: {
        SettingsBase,
    },
    data() {
        return {

            // 
            is_form_dense: Utils.isSmartphoneHorizontal(),

            // 
            twitter_active_tab: [
                {text: '', value: 'Search'},
                {text: '', value: 'Timeline'},
                {text: '', value: 'Capture'},
            ],

            // 
            tweet_hashtag_position: [
                {text: '', value: 'Prepend'},
                {text: '', value: 'Append'},
                {text: '', value: 'PrependWithLineBreak'},
                {text: '', value: 'AppendWithLineBreak'},
            ],

            // 
            tweet_capture_watermark_position: [
                {text: '', value: 'None'},
                {text: '', value: 'TopLeft'},
                {text: '', value: 'TopRight'},
                {text: '', value: 'BottomLeft'},
                {text: '', value: 'BottomRight'},
            ],

            // 
            is_loading: true,

            // 
            is_twitter_password_auth_sending: false,

            // 
            twitter_password_auth_dialog: false,

            // Twitter 
            twitter_screen_name: '',
            twitter_password: '',
            twitter_password_showing: false,
        };
    },
    computed: {
        // SettingsStore / UserStore  this.settingsStore / this.userStore 
        // ref: https://pinia.vuejs.org/cookbook/options-api.html
        ...mapStores(useSettingsStore, useUserStore),
    },
    async created() {

        // 
        await this.userStore.fetchUser();

        // 
        this.is_loading = false;
    },
    methods: {
        async loginTwitterAccountWithPasswordForm() {
            // 
            if (this.userStore.is_logged_in === false) {
                this.$message.warning('KonomiTV ');
                await Utils.sleep(0.01);
                this.twitter_password_auth_dialog = false;
                return;
            }
            this.twitter_password_auth_dialog = true;
        },

        async loginTwitterAccountWithPassword() {

            // 
            if ((this.$refs.twitter_form as any).validate() === false) {
                return;
            }

            // Twitter  API 
            this.is_twitter_password_auth_sending = true;
            const result = await Twitter.authWithPassword({
                screen_name: this.twitter_screen_name,
                password: this.twitter_password,
            });
            this.is_twitter_password_auth_sending = false;
            if (result === false) {
                return;
            }

            // 
            await this.userStore.fetchUser(true);
            if (this.userStore.user === null) {
                this.$message.error('');
                return;
            }

            //  Twitter  updated_at 
            //  updated_at  updated_at 
            // ref: https://stackoverflow.com/a/12192544/17124142 (ISO8601 )
            const current_twitter_account = [...this.userStore.user.twitter_accounts].sort((a, b) => {
                return (a.updated_at < b.updated_at) ? 1 : ((a.updated_at > b.updated_at) ? -1 : 0);
            })[0];

            this.$message.success(`Twitter @${current_twitter_account.screen_name} `);

            // 
            (this.$refs.twitter_form as any).reset();
            this.twitter_password_auth_dialog = false;
        },

        async logoutTwitterAccount(screen_name: string) {

            // Twitter  API 
            const result = await Twitter.logoutAccount(screen_name);
            if (result === false) {
                return;
            }

            // 
            await this.userStore.fetchUser(true);

            this.$message.success(`Twitter @${screen_name} `);
        },
    }
});

